
.custom-slide {
    padding-top: 50px;
    width: 200px;
    transform: translateX(-250px);
    transition: .3s;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
    height: 100vh;
  }
  
  .custom-slide li {
    margin-bottom: 30px;
    border: 1px solid black;
    padding: 5px 10px;
    margin-right: 20px;
    border-radius: 2px;
    cursor: pointer;
    transition: .08s;
  }
  .custom-slide li:hover {
    color: white;
    background-color: black;
  }
  
  .slide-show {
    transform: translateX(0);
  }